import { Component, Host, h, Prop } from '@stencil/core';

@Component({
	tag: 'o-app-bar',
	styleUrl: 'o-app-bar.scss',
	shadow: true
})
export class OAppBar {
	/** A description that gets read by assistive devices. */
	@Prop({ reflect: true }) label?: string = '';

	render() {
		return (
			<Host>
				<header
					aria-label={this.label}
					class="fixed z-10 top-0 left-0 w-full h-16 flex items-center justify-between  bg-[image:--oui-theme-app-bar-color-background-default] px-4"
				>
					<div class="items-center gap-4">
						<slot name="left"></slot>
					</div>

					<div class="justify-center gap-4">
						<slot name="center"></slot>
					</div>

					<div class="items-center gap-4">
						<slot name="right"></slot>
					</div>
				</header>
			</Host>
		);
	}
}
